body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.clearing {
	clear: both;
}

label {
  font-size: 0; }

.list-summary {
  border: 1px solid #999;
  border-radius: 8px;
  margin: 0 8px 8px 0;
  overflow: hidden;
  padding: 8px; }
  .list-summary a {
    color: #333; }
    .list-summary a:hover {
      text-decoration: none; }
  .list-summary .list-header {
    font-weight: bold; }
  .list-summary .list-controls {
    float: right; }
    .list-summary .list-controls .list-status {
      float: left; }
    .list-summary .list-controls .btn-danger {
      float: left;
      margin-left: 15px; }

.list-body {
  background: #fff;
  display: block;
  line-height: 1.5em;
  max-height: 3em;
  overflow: hidden;
  position: relative; }
  .list-body::before {
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fff 80%);
    content: "\200C";
    display: block;
    position: absolute;
    right: 0;
    text-align: center;
    top: 1.5em;
    width: 4em; }

.list-created-by {
  float: left;
  margin: 6px 0; }

.lists-list .tabs {
  display: flex;
  justify-content: center;
  margin: 6px 0; }
  .lists-list .tabs ul {
    border: 1px solid #424a6c;
    border-radius: 12px;
    float: left;
    list-style-type: none;
    margin: 0;
    overflow: hidden;
    padding: 0; }
  .lists-list .tabs span {
    cursor: pointer;
    display: inline-block;
    padding: 6px 12px; }
    .lists-list .tabs span.selected {
      background-color: #424a6c;
      color: #fff; }

.lists-list .create-list {
  margin: 8px 0; }

.lists-list .top-level-lists-control .form-check-input {
  margin: 8px 0;
  position: relative; }

.lists-list .top-level-lists-control label {
  font-size: 1rem; }

.btn-primary {
  margin-left: 8px; }

.editable-text-field input {
  padding: 6px 12px; }

.editable-text-field .placeholder {
  color: #aaa; }

.editable-text-field .btn-primary {
  margin-left: 8px; }

.editable-text-field .text {
  display: inline-block;
  margin: 6px 0;
  max-height: 3em;
  overflow: hidden;
  position: relative; }
  .editable-text-field .text.expanded {
    max-height: none; }
    .editable-text-field .text.expanded .fader {
      display: none; }
  .editable-text-field .text .fader {
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fff 80%);
    display: block;
    height: 1.5em;
    position: absolute;
    right: 0;
    text-align: center;
    top: 1.5em;
    width: 4em; }

.editable-text-field button.show-more {
  float: right; }

.editable-text-field .controls {
  margin-bottom: 6px; }

.editable-text-field .hint {
  color: #666;
  margin-left: 6px; }
  .editable-text-field .hint a {
    color: #666;
    text-decoration: underline; }

.item-header {
  font-weight: bold; }
  .item-header .editable-text-field {
    float: left;
    margin-left: 0; }
    .item-header .editable-text-field.editing {
      float: none; }
    .item-header .editable-text-field .placeholder {
      display: inline-block;
      padding: 6px 0; }

.item-body {
  clear: both; }

.item-container {
  border: 1px solid #999;
  border-radius: 8px;
  margin: 0 8px 8px 0;
  overflow: hidden;
  padding: 8px; }
  .item-container label {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    width: 1px; }
  .item-container .order {
    float: left;
    margin-right: 8px;
    padding: 6px 0; }
  .item-container button.create-childlist {
    float: right; }
  .item-container button.move-up {
    margin-right: 6px; }
  .item-container .child-list {
    float: right;
    padding: 6px 0; }

.list-detail-controls {
  float: right; }
  .list-detail-controls .list-status {
    float: left; }
  .list-detail-controls .btn-danger {
    float: left;
    margin-left: 15px; }

.list-detail .list-name label {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px; }

.list-detail .list-name span {
  font-size: 150%;
  font-weight: bold; }

.list-detail .list-description label {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px; }

.list-detail .privacy-warning {
  color: #666; }

.valid-feedback {
  display: block;
  color: #28a745;
  font-size: 80%;
  margin-top: .25rem;
  width: 100%; }
  .valid-feedback::before {
    content: "\2714   "; }

.invalid-feedback {
  color: #dc3545;
  font-size: 80%;
  margin-top: .25rem;
  width: 100%; }
  .invalid-feedback::before {
    content: "\2718   "; }

.flash-error {
  border-radius: 8px;
  padding: 8px; }
  .flash-error.error {
    border: 1px solid #dc3545;
    color: #dc3545; }
    .flash-error.error::before {
      content: "\2718   "; }
  .flash-error.success {
    border: 1px solid #28a745;
    color: #28a745; }
    .flash-error.success::before {
      content: "\2714   "; }

